import React from 'react';
import '../mainCss/about.css';
import data from '../../data';
import SectionHeader from '../jsx/SectionHeader';
import SectionSubHeader from '../jsx/SectionSubHeader';
import SectionText from '../jsx/SectionText';
import Button from '../jsx/Button';

const { about } = data;

const About = () => {
  return (
    <div className='about' id='about'>
      <div className="container">
        <div className="split">
          <div className="left">
            <SectionHeader text={about.header} />
            <SectionSubHeader text={about.subHeader}/>
            <SectionText text={about.text}/>
            <Button text='learn more' target='#'/>
          </div>
          <div className="right">
            <div className="image">
              <img src={about.image} className='img' alt="" />
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default About
