import { FaDAndD } from 'react-icons/fa';
import React, { useState } from 'react';
import '../css/Header.css';

const Header = ({ media }) => {

  const [show, setShow] = useState(false);
  const resetMenu = () => {
    if(show) setShow(false);
  }

  return (
    <header className='narrow wide'>
      <FaDAndD className='icon'/>

      <div className="bars" onClick={() => setShow(!show)}>
        <span className="bar top"></span>
        <span className="bar mid"></span>
        <span className="bar bottom"></span>
      </div>
      <ul className={`links ${show&& 'show'}`}>
        <li className='active'><a href="#" onClick={resetMenu}>home</a></li>
        <li><a href="#about" onClick={resetMenu}>about</a></li>
        <li><a href="#portfolio" onClick={resetMenu}>portfolio</a></li>
        <li><a href="#contact-us" onClick={resetMenu}>contact us</a></li>
      </ul>
    </header>
    )
}
  



export default Header;