import React from 'react'
import '../css/Input.css'


const Input = ({ label, type, tip }) => {
  if(type === 'msg') {
    return (
      <div className="input-field">
        <div className="label">{label}</div>
        <textarea className='input msg' placeholder={tip}></textarea>
      </div>
    )
  }
  
  return (
    <div className='input-field'>
      <div className="label">{label}</div>
      <input type={type} placeholder={tip} className='input'/>
    </div>
  )
}

export default Input
