import React from 'react'
import '../css/Card.css'


const Card = ({ img, label, text, target }) => {
  return (
    <div className='card'>
      <div className="image">
        <img src={img} alt='' className='img'/>
      </div>
      <div className="label">{label}</div>
      <div className="text">{text}</div>
      <div className="link">
        <a  href={target} target='_blank'>visit site</a>
      </div>
    </div>
  )
}

export default Card
