import React from 'react';
import '../mainCss/portfolio.css';
import data from '../../data';
import Card from '../jsx/Card';
import SectionHeader from '../jsx/SectionHeader';
import SectionSubHeader from '../jsx/SectionSubHeader';
import Button from '../jsx/Button';

const { portfolio } = data;

const Portfolio = () => {
  return (
    <div className='portfolio' id='portfolio'>
      <div className="container">
        <SectionHeader text={portfolio.header}/>
        <SectionSubHeader text={portfolio.subHeader}/>
        <Button text='show more' target='#'/>
        <div className="cards">
          {
            portfolio.cards.map(card => {
              return <Card img={card.image} label={card.header} text={card.text} target={card.target}/>
            })
          }
        </div>
      </div>
    </div>
  )
}

export default Portfolio
