import React from 'react'
import '../mainCss/skills.css';
import data from '../../data';
import SkillBar from '../jsx/SkillBar';
import SectionHeader from '../jsx/SectionHeader';
import SectionSubHeader from '../jsx/SectionSubHeader';
import { FaJsSquare } from 'react-icons/fa'


const { skills } = data;

const displaySkillBars = (skills) => {
  return (
    skills.bars.map(bar => {
      const { Icon, label, percent } = bar;
      return <SkillBar label={label} percentage={percent} icon={<Icon/>} />
    })
  )
}

const Skills = () => {
  return (
    <div className='skills' id='skills'>
      <div className="container">
        <div className="split">
          <div className="left">
            <SectionHeader text={skills.header}/>
            <SectionSubHeader text={skills.subHeader} />
            <div className="bars">
              {displaySkillBars(skills)}
            </div>
          </div>
          <div className="right">
            <div className="image">
              <img src={skills.image} alt="" className='img' />
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}


export default Skills
