import React from 'react'
import '../mainCss/contactUs.css';
import Button from '../jsx/Button';
import Input from '../jsx/Input';
import SectionHeader from '../jsx/SectionHeader';
import SectionSubHeader from '../jsx/SectionSubHeader';
import data from '../../data';


const { contactUs } = data;

const ContactUs = () => {
  return (
    <div className='contact-us' id='contact-us'>
      <div className="container">
        <SectionHeader text={contactUs.header}/>
        <SectionSubHeader text={contactUs.subHeader} />
        <form>
          <Input label='name' tip='name'/>
          <Input label='email' tip='email'/>
          <Input label='message' tip='message' type='msg' />
          <Button text='submit' type='submit'/>
        </form>
      </div>      
      <div className="shape"></div>
      <div className="remove-shape"></div>
    </div>
  )
}



export default ContactUs
