import React from 'react';
import '../mainCss/landing.css';
import data from '../../data';
import Button from '../jsx/Button'
import Header from '../jsx/Header'
const { landing } = data;

const Landing = () => {

  return (
    <div className='landing' id='landing'>
      <div className="container">
        <Header media='narrow'/>
        <div className="split">
          <div className="left">
            <img className='img' src={landing.image} alt="" />
          </div>
          <div className="right">
            <div className="main-header">{landing.header}</div>
            <div className="main-sub-header">{landing.subHeader}</div>
            <div className="main-text">{landing.text}</div>
            <Button text='contact us' target='contact-us' />
          </div>
        </div>
      </div>
    </div>
  )
}

export default Landing


